html, body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  * {
    box-sizing: inherit;
  }
}

@include keyframes (fade_out) {
    0% {
        opacity: 0;
    }
    99% {
        opacity: 1;
        z-index: 20;
    }
    100%{
      display: none;
    }
};

%state {
  transition: .3s opacity ease-in;
  &--hide {
    // @include animation( .3s ease-in fade_out forwards );
    display: none;
  }
}

.state { @extend %state; }
.state--hide { @extend %state--hide; }
